import React , {useEffect , useState} from 'react'

import { pacDetails } from '../../api/login/Login'
import { Link, useParams } from 'react-router-dom'
import { Form, Card, Badge, Row, Col,Alert } from 'react-bootstrap';
import { TreeSelect, Table,Typography, Divider, Empty, Spin, Collapse, Pagination } from 'antd';
import { Button } from '@mui/material';
const { Title, Paragraph } = Typography;
const { Panel } = Collapse;


const ItemsPerPage = 10;
const ResellerPackageAction = () => {

       const param = useParams()
        const [dataDetails, setDataDetails] = useState(null)
          const [currentPages, setCurrentPages] = useState({});


   const packageDetail = async () => {
        
        try {
            const res = await pacDetails(param?.id)
            setDataDetails(res?.data?.data)
            // changeHandle(res?.data?.data?.prices[0])
        } catch (error) {

        }
    }
       useEffect(() => {
            packageDetail()
        }, [])

        const handlePageChange = (page, serviceId) => {
            setCurrentPages(prevPages => ({
              ...prevPages,
              [serviceId]: page
            }));
          };

          const getColumns = () => [
            {
              title: 'S No.',
              key: 'index',
              render: (text, record, index) => index + 1,
              onHeaderCell: () => ({
                style: {
                  backgroundColor: '#cfe2ff',
                },
              }),
            },
            {
              title: 'Start Amount',
              dataIndex: 'start_amt',
              key: 'start_amt',
              onHeaderCell: () => ({
                style: {
                  backgroundColor: '#cfe2ff',
                },
              }),
            },
            {
              title: 'End Amount',
              dataIndex: 'end_amt',
              key: 'end_amt',
              onHeaderCell: () => ({
                style: {
                  backgroundColor: '#cfe2ff',
                },
              }),
            },
            {
                title: 'Charges',
                dataIndex: 'charge',
                key: 'charge',
                onHeaderCell: () => ({
                  style: {
                    backgroundColor: '#cfe2ff',
                  },
                }),
              },
              {
                title: 'Charges Type',
                dataIndex: 'charge_type',
                key: 'charge_type',
                onHeaderCell: () => ({
                  style: {
                    backgroundColor: '#cfe2ff',
                  },
                }),
              },
              {
                title: 'Reseller Commision',
                dataIndex: 'reseller_user_comm',
                key: 'reseller_user_comm',
                onHeaderCell: () => ({
                  style: {
                    backgroundColor: '#cfe2ff',
                  },
                }),
              },
              {
                title: 'Reseller Commision Type',
                dataIndex: 'reseller_user_comm_type',
                key: 'reseller_user_comm_type',
                onHeaderCell: () => ({
                  style: {
                    backgroundColor: '#cfe2ff',
                  },
                }),
              },

              
            
          
          ];

        const renderOtherCommTables = () => {
          if (!dataDetails?.OtherComm || dataDetails.OtherComm.length === 0) {
            return <Empty description="No commission data available" />;
          }
      
          return dataDetails.OtherComm.map((service) => {
            const currentPage = currentPages[service._id] || 1;
      
            return (
              <div key={service._id}>
                <div className='d-flex justify-content-between align-items-center'>
                <Title level={4}>{service.service}</Title>
                <Link to={`/reseller-package-commission-update/${service?.commision_id}`}>
             
                <Button
                                  
                                  color="primary"
                                  variant="outlined"
                                  
                                  sx={{
                                    backgroundColor: "var(--bs-primary1)",
                                    color :"white",
                                    borderColor:"var(--bs-primary1)",
                                    // borderRadius: 2,
                                    px: 1,
                                    mb:2,
                                  
                                  }}
                                >
                                 
                                   Edit Commission 
                                 
                                
                                </Button>
                                </Link>
                </div>
              
                <div>
                        
                </div>
                {/* <Paragraph>Commission Name: {service.commision_name}</Paragraph> */}
                <Table
                  dataSource={service.commision}
                  columns={getColumns(service.service)}
                  pagination={{
                    current: currentPage,
                    pageSize: ItemsPerPage,
                    total: service.commision.length,
                    onChange: (page) => handlePageChange(page, service._id)
                  }}
                  rowKey="_id"
                  scroll={{ x: true }}
                />
                <Divider />
              </div>
            );
          });
        };    

        function createMarkup(data) {
            return { __html: data };
        }
  return (
   <>
      <div className="PageHeading">
                <h1>Package Details</h1>
            </div>

         <div className="ContentArea">
                      <h3 style={{fontSize:'19px' , color:"var(--bs-nav)" , padding:'10px'}}>{dataDetails?.package_name}</h3>
                      <div className="card">
                          <div className="card-header card-header-plannhrad">
                              <h2 className='plannhrad'>Subscription Plan</h2>
                          </div>
                          <div className="card-body">
                              <div className="form-row">
                                
                                  <div className="col-lg-12">
                                      <div className='details-set'>
                                    
              <Card>
               
                <div className="card-body">
                  <div className="form-row">
                    <div className="col-lg-12">
                      <div className='details-set'>
                        <p>
                          <div
                            className="products-details-tab-content"
                            dangerouslySetInnerHTML={createMarkup(
                              dataDetails?.description
                            )}
                          />
                        </p>
                   
      
                        {dataDetails?.OtherComm?.length > 0 && (
                          <Collapse className='mt-2'>
                            <Panel header="Commissions" key="2">
                              {renderOtherCommTables()}
                            </Panel>
                          </Collapse>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </Card>
              </div>
              </div>
              </div>
              </div>
              </div>
              </div>
              
                                         
      
      
                                 
                           
                        
                   
                 
   
   
   </>
  )
}

export default ResellerPackageAction